export const MessageThreadViewStyle = (
  message: CometChat.BaseMessage,
  theme: CometChatTheme,
  loggedInUser: any,
  messageListStyle: MessageListStyle = {}
) => {
  if (
    (!message.getSender() ||
      message.getSender().getUid() === loggedInUser?.getUid())
  ) {
    return {
      height: "100%",
      width: "100%",
      border: "none",
      borderRadius: "0",
      background: "transparent",
      buttonIconTint: messageListStyle.threadReplyIconTint || theme.palette.getAccent600(),
      display: "flex",
      flexFlow: "row-reverse",
      alignItems: "center",
      buttonTextColor: messageListStyle?.threadReplyTextColor || theme.palette.getAccent(),
      buttonTextFont: messageListStyle?.threadReplyTextFont || fontHelper(theme.typography.text2),
      iconHeight: "15px",
      iconWidth: "15px",
      gap: "5px",
    };
  } else {
    return {
      height: "100%",
      width: "100%",
      border: "none",
      borderRadius: "0",
      background: "transparent",
      buttonIconTint: messageListStyle.threadReplyIconTint || theme.palette.getAccent600(),
      display: "flex",
      flexFlow: "row",
      alignItems: "center",
      gap: "5px",
      buttonTextColor: messageListStyle?.threadReplyTextColor || theme.palette.getAccent(),
      buttonTextFont: messageListStyle?.threadReplyTextFont || fontHelper(theme.typography.text2),
      iconHeight: "15px",
      iconWidth: "15px"
    };
  }
};

